.start{
      width: 95%;
      height: 100vh;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      overflow: hidden;
}

.start h1{
      font-size: 3.4em;  
      color: white;
}

.start span{
    border: 3px solid black;
    margin: 10px;
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 150px;
    background-color: white;
    font-weight: bold;
}

.start p{
    margin-bottom: 2em;
    color: #ecfa00; 
}

.start button{
      margin-bottom: 30px; 
}


@media (max-width: 800px) {
    .start h1{
        font-size: 3em;  
  }
  
  .start p{
      margin-bottom: 1em;
  }
}