.end{
    width: 95%;
    height: 100vh;
    padding: 10px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: flex;
    overflow: hidden;
}

.end h1{
    color: white;
    font-size: 50px;
}

.end span{
    width: 200px;
    height: 200px;
    border: 2px solid black;
    font-size: 100px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    background-color: white;
}

.end span img{
    width: 60%;
}

.end p{
    color: white;
}

.end p h5{
    color: yellow;
    display: inline;
    font-size: 18px;
}

.end button{
    margin-bottom: 30px;
}





@media (max-width:800px){
    .end h1{
        font-size: 40px;
    }
}