body{
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0;
    background-image: linear-gradient(180deg, rgba(9,35,157,1) 10%, rgba(0,0,0,1)50%);
    height: 100vh;
    padding: 10px;
    overflow: hidden;
}

button{
      background-color: #1646a0;
      color: #fff;
      padding: 0 45px;
      border: 2px solid white;
      border-radius: 25px;
      height: 50px;
      text-transform: uppercase;
      font-weight: bold;
      cursor: pointer;
      transition: 400ms;
}

button:hover{
     background-color: #0923af;
}


footer {
      position: fixed;
      bottom: 10px;
      width: 100%;
      text-align: center;
      padding: 5px;
      color: rgba(255, 255, 255, 0.26);
      font-size: 14px;
}



@media (max-width:800px){
      button{
            padding: 0 35px;
            height: 45px;
      }
}