.gameplay{
      color: white;
      width: 95%;
      height: 100vh;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      overflow: hidden;
      
}

.gameplay h1{
       font-size: 2em;
}

.gameplay span{
    font-weight: bold;
}

.tip span{
    color: #ecfa00;
}

.wordContainer{
     margin: 5px;
     padding: 5px;
     border: 15px solid #ecfa00;
     display: flex;
}

.letter, .blackSquare{
     font-size: 60px;
     display: flex;
     justify-content: center;
     align-items: center;
     border: 3px solid black;
     width: 80px;
     height: 80px;
     text-transform: uppercase;
     background-color: white;
     margin: 5px;
     font-weight: bold;
     color: black;
}  

.letterContainer{
       margin-bottom: 10px;
       color: white;
}

.letterContainer form{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.letterContainer input{
    height: 50px;
    width: 50px;
    font-size: 2em;
    text-align: center;
    margin-right: 1em;
}

.wrongLettersContainer{
      color: white;
}

.wrongLettersContainer h4{
     margin: 5px;
}


.reloadBtn{
      background-color: transparent;
      border: 1px solid transparent;
      color: red;
      padding: 2px 10px;
      font-size: 12px;
      margin: 10px 0px 20px 0px;
}

.reloadBtn:hover{
    color: #ecfa00;
    background-color: transparent;
}








/*RESPONSIVO ------------------------------------------*/

@media (max-width:800px) {
  .gameplay h1{
         font-size: 2em;
  }
  
  .wordContainer{
       padding: 2px;
       width: 90%;
       border: 10px solid #ecfa00;
       display: flex;
       justify-content: center;
       align-items: center;
  }
  
  .letter, .blackSquare{
       font-size: 30px;
       line-height: 1;
       border: 2px solid black;
       width: 30px;
       height: 30px;
  }  
  
  .letterContainer{
         margin-bottom: 1em;
  }
  
  .letterContainer form{
      margin: 10px;
  }
  
  .letterContainer input{
      height: 30px;
      width: 30px;
      font-size: 1.5em;
      margin-right: 14px;
  }
}